
const diffObject = t => [
    t.delete, // Symbol('delete')   
    t.primitive, //   ,   
    {
        [t.array]: t`?`(true), // ,   ,     Number
        [t.raw]: t`?`(true), // ,      ,   
        [t(String)]: t`?`(diffObject),
        [t(Symbol)]: t`?`(diffObject) //   
    }
];

const D = {
    delete: Symbol('delete'),
    array: Symbol('array'),
    raw: Symbol('raw'),
    meta: Symbol('meta'),
    new: Symbol('new')
};

export const isPrimitive = value => {
    return value !== new Object(value);
}

export const raw = value => {
    if (isPrimitive(value)) {
        return value;
    }
    // return {
    //     ...value,
    //     [D.raw]: true,
    //     [D.new]: value
    // }
    value[D.raw] = true;
    value[D.new] = value;
    return value;
}


// TODO:    
export function diff(A, B) {
    if (A === B) { //   ( ),   ( )
        return {};
    }
    if (isPrimitive(A)) {
        if (isPrimitive(B)) {
            return B;
        } else {
            return raw(B);
        }
    } else {
        if (isPrimitive(B)) {
            return B;
        } else {
            if (typeof A === 'function') { //    ,     
                if (A === B) {
                    return {};
                } else if (A.toString() === B.toString()) {
                    return {};
                }
                return raw(B);
            } else if (Array.isArray(A)) {
                if (Array.isArray(B)) { //  
                    const lA = A.length;
                    const lB = B.length;
                    const max = lA > lB ? lA : lB;
                    const min = lA < lB ? lA : lB;
                    const result = {};
                    for (let i = 0; i < max; i++) {
                        if (i < min) { //   
                            const indexDiff = diff(A[i], B[i]); //  
                            if (isPrimitive(indexDiff) || indexDiff === D.delete || Object.keys(indexDiff).length > 0 || indexDiff[D.raw]) {
                                result[String(i)] = indexDiff; //   
                            }
                        } else if (lA < lB) {
                            result[String(i)] = B[i]; //  
                        } else {
                            result[String(i)] = D.delete; //  
                        }
                    }
                    if (Object.keys(result).length > 0) {
                        result[D.array] = true;
                    }
                    return result;
                } else { //   
                    return raw(B);
                }
            } else { //  
                if (typeof B === 'function' || Array.isArray(B)) { //   
                    return raw(B);
                } else { //  
                    const result = {};
                    for (const key of Object.keys(B)) {
                        if (!(key in A)) {
                            result[key] = raw(B[key]); //     
                        }
                    }
                    for (const key of Object.keys(A)) {
                        if (key in B) {
                            const keyDiff = diff(A[key], B[key]); //  
                            if (isPrimitive(keyDiff) || keyDiff === D.delete || Object.keys(keyDiff).length > 0 || keyDiff[D.raw]) {
                                result[key] = keyDiff; //   
                            }
                        } else {
                            result[key] = D.delete; //   
                        }
                    }
                    return result;
                }
            }
        }
    }
}

diff.symbols = D;

